<template>
  <div class="phonecon-nav">
  	<div class="swiper-container" id="navSwiper">
  	    <div class="swiper-wrapper">
  	        <div class="swiper-slide">
  				<ul>
					<li :class="getRouterName == 'index' ? 'active' : ''"><router-link to="/">优居科技</router-link></li>
					<li :class="getRouterName == 'disinfection' ? 'active' : ''"><router-link to="/disinfection">快速消毒</router-link></li>
					<li :class="getRouterName == 'antibacterial' ? 'active' : ''"><router-link to="/antibacterial">长效抗菌</router-link></li>
					<li :class="getRouterName == 'safety' ? 'active' : ''"><router-link to="/safety">安全无毒</router-link></li>
  				</ul>
  			</div>
  	        <div class="swiper-slide">
  				<ul>
					<li :class="getRouterName == 'photocatalysis' ? 'active' : ''"><router-link to="/photocatalysis">专利科技</router-link></li>
					<li :class="getRouterName == 'cxsdkjfw' ? 'active' : ''"><router-link to="/cxsdkjfw">整体方案</router-link></li>
					<li :class="getRouterName == 'join' ? 'active' : ''"><router-link to="/join">招商加盟</router-link></li>
					<li :class="getRouterName == 'reservation' ? 'active' : ''"><router-link to="/reservation">预约服务</router-link></li>	
  				</ul>
  			</div>
  	    </div>
  	    <!-- 如果需要分页器 -->
  	    <div class="swiper-pagination"></div>
  	</div>
  </div>
</template>

<script>
import Swiper from 'swiper'
	
export default {
  name: 'phoneNav',
  data () {
    return {}
  },
  computed:{
  	  getRouterName () {
  		  return this.$route.name
  	  }
  },
  mounted() {
  	let navSwiper = document.getElementById('navSwiper')
  	new Swiper( navSwiper , {
  		// loop : true,
  		pagination: {
  			el: '.swiper-pagination',
  			clickable :true,
  		}
  	})
  }
}
</script>

<style scoped>
	
</style>



